import * as React from "react";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";

const CategoryButton = styled(Button)({
  height: "45px",
  width: "100px",
  fontSize: "1.3em",
  boxShadow: "none",
  padding: "0.5em 0.5em",
  backgroundColor: "white",
  color: "black",
  marginTop: "10px",
  fontFamily: [
    "-apple-system",
    "BlinkMacSystemFont",
    '"Segoe UI"',
    "Roboto",
    '"Helvetica Neue"',
    "Arial",
    "KCC-Ganpan",
    "sans-serif",
    '"Apple Color Emoji"',
    '"Segoe UI Emoji"',
    '"Segoe UI Symbol"',
  ].join(","),
  "&:hover": {
    backgroundColor: "#F0DBDB",
    borderColor: "#F0DBDB",
    boxShadow: "none",
  },
  "&:active": {
    boxShadow: "F0DBDB",
    backgroundColor: "#F0DBDB",
    borderColor: "#F0DBDB",
  },
  "&:focus": {
    backgroundColor: "#F0DBDB",
    borderColor: "#F0DBDB",
  },
});

const CategoryButtons = ({ onClick, selected, children }) => {
  return (
    <CategoryButton
      onClick={onClick}
      selected={selected}
      variant="contained"
      disableRipple
    >
      {children}
    </CategoryButton>
  );
};

export default CategoryButtons;
