import * as React from "react";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";

const BootstrapButton = styled(Button)({
  height: "43px",
  width: "280px",
  boxShadow: "none",
  textTransform: "none",
  fontSize: "1.3em",
  padding: "0.5em 0.5em",
  border: "1px solid",
  borderRadius: "10px",
  color: "#9D550E",
  lineHeight: 1.5,
  backgroundColor: "#F5EBE0",
  borderColor: "#F5EBE0",
  fontFamily: [
    "-apple-system",
    "BlinkMacSystemFont",
    '"Segoe UI"',
    "Roboto",
    '"Helvetica Neue"',
    "Arial",
    "KCC-Ganpan",
    "sans-serif",
    '"Apple Color Emoji"',
    '"Segoe UI Emoji"',
    '"Segoe UI Symbol"',
  ].join(","),
  "&:hover": {
    backgroundColor: "#F0DBDB",
    borderColor: "#F0DBDB",
    boxShadow: "none",
  },
  "&:active": {
    boxShadow: "F0DBDB",
    backgroundColor: "#F0DBDB",
    borderColor: "#F0DBDB",
  },
  /*   "&:focus": {
    boxShadow: "0 0 0 0.2rem rgba(255,130,0,.5)",
  }, */
});

const CustomizedButtons = (props) => {
  return <BootstrapButton variant="contained" disableRipple {...props} />;
};

export default CustomizedButtons;
