import React from "react";
import styled from "styled-components";
import GOOD from "../img/logoName.png";
import Line from "../img/b_line.png";
import { Link } from "react-router-dom";

const Img = styled.img`
  width: 100%;
  overflow: hidden;
  @media (max-width: 359px) {
  }
`;

const LogoName = styled.div`
  font-family: "KCC-Ganpan";
  color: #a45309;
  font-size: 24px;
  margin-top: 1em;
`;

const Top = () => {
  return (
    <>
      <LogoName>Good Choice</LogoName>
      <Img src={Line} />
    </>
  );
};

export default Top;
