import React from "react";
import styled from "styled-components";
import Wrapper from "./Wrapper";
import HamImg from "../img/Hamster.png";

const Back = styled.div`
  border: none;
  border-radius: 10px;
  background-color: #dddddd;
  text-align: center;
  font-family: "Jeju-Hallasan";
  height: 180px;
  width: 240px;
  margin: 3.5em 2em;
`;

const Image = styled.img`
  position: relative;
  width: 85px;
  height: 85px;
  margin-top: 20px;
  border-radius: 10px;
`;

const Text = styled.div`
  position: relative;
  line-height: 70px;
`;

const LoadMenu = () => {
  return (
    <Wrapper>
      <Back>
        <Image src={HamImg} />
        <Text> ?</Text>
      </Back>
    </Wrapper>
  );
};

export default LoadMenu;
